  let onChangeField = (name: string, value: unknown | null) => {
    value = Number(value);
    if (!value) {
      value = null;
    }
    props.onRefreshCalculate({ [name]: value });
  };

  let onChangeSelect = (
    e: React.ChangeEvent<{ name: string; value: unknown }>
  ) => {
    let value = Number(e.target.value);
    props.onRefreshCalculate({ [e.target.name]: value });
  };

  React.useEffect(() => {
    props.onRefreshCalculate({});
  }, []);

  let months = [
    // { value: 12, name: "1 nm (12 thng)" },
    // { value: 24, name: "2 nm (24 thng)" },
    { value: 36, name: "3 nm (36 thng)" },
    { value: 48, name: "4 nm (48 thng)" },
    { value: 60, name: "5 nm (60 thng)" },
    { value: 72, name: "6 nm (72 thng)" },
    { value: 84, name: "7 nm (84 thng)" },
  ];

  const classes = useStyles();

